<template>
  <div>
  <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="9">
          <el-input placeholder=""
          v-model="queryinfo.query" clearable @clear="getUserList">
        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
      </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
          </template >
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content=""
            placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-setting" size="mini"
              @click="setRole(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryinfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryinfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%" @close="addDialogClosed">
    <!--  -->
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
      <el-form-item label="" prop="username">
        <el-input v-model="addForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model="addForm.password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="addForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="mobile">
        <el-input v-model="addForm.mobile"></el-input>
      </el-form-item>
    </el-form>
    <!--  -->
    <span slot="footer" class="dialog-footer">
      <el-button @click="addDialogVisible = false"> </el-button>
      <el-button type="primary" @click="adduser"> </el-button>
    </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules"
      ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="setRoleDialogVisible"
    width="50%" @close="setRoleDialogClosed">
  <div>
    <p>: {{userInfo.username}}</p>
    <p>: {{userInfo.role_name}}</p>
    <p>
      <el-select v-model="selectedRoleId" placeholder="">
        <el-option v-for="item in roleList" :key="item.id" :label="item.roleName" :value="item.id">
        </el-option>
      </el-select>
    </p>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRoleDialogVisible = false"> </el-button>
    <el-button type="primary" @click="saveRoleInfo"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;

      if (regEmail.test(value)) {
        // 
        return cb();
      }
      cb(new Error(''));
    };
    // //
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;

      if (regMobile.test(value)) {
        // 
        return cb();
      }
      cb(new Error(''));
    };
    return {
      // 
      queryinfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [
          {
            required: true,
            message: '',
            tigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: '3~10',
            tigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '',
            tigger: 'blur'
          },
          {
            min: 6,
            max: 15,
            message: '6~15',
            tigger: 'blur'
          }
        ],
        email: [
          {
            required: true,
            message: '',
            tigger: 'blur'

          },
          {
            validator: checkEmail,
            tigger: 'blur'
          },
          {
            min: 12,
            message: '',
            tigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            message: '',
            tigger: 'blur'
          },
          {
            validator: checkMobile,
            tigger: 'blur'
          },
          {
            min: 11,
            max: 11,
            message: '12',
            tigger: 'blur'
          }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        email: [
          {
            required: true,
            message: '',
            tigger: 'blur'
          },
          {
            validator: checkEmail,
            tigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            message: '',
            tigger: 'blur'
          },
          {
            validator: checkMobile,
            tigger: 'blur'
          }
        ]
      },
      // 
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      roleList: [],
      // Id
      selectedRoleId: ''
    };
  },
  created () {
    this.getUserList();
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('users', { params: this.queryinfo });
      if (res.meta.status !== 200) {
        return this.$message.error('');
      }
      this.userlist = res.data.users;
      this.total = res.data.total;
    },
    //  pagesize 
    handleSizeChange (newSize) {
      this.queryinfo.pagesize = newSize;
      this.getUserList();
    },
    //   
    handleCurrentChange (newSize) {
      this.queryinfo.pagenum = newSize;
      this.getUserList();
    },
    //  switch 
    async userStateChanged (userinfo) {
      const { data: res } = await this.$http.put(`users/${userinfo.id}
      /state/${userinfo.mg_state}`);
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state;
        return this.$message.error('');
      }
      this.$message.success('');
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields();
    },
    // 
    adduser () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return null;
        // 
        const { data: res } = await this.$http.post('users', this.addForm);

        if (res.meta.status !== 201) {
          this.$message.error('');
        }

        this.$message.success('');
        // 
        this.addDialogVisible = false;
        // 
        this.getUserList();
      });
    },
    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('users/' + id);

      if (res.meta.status !== 200) {
        return this.$message.error('');
      }

      this.editForm = res.data;
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRulesRef.resetFields();
    },
    // 
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        // eslint-disable-next-line no-useless-return
        if (!valid) return;
        // 
        const { data: res } = await this.$http.put('users/' + this.editForm.id, {
          email: this.editForm.email,
          mobile: this.editForm.mobile
        });

        if (res.meta.status !== 200) {
          return this.$message.error('');
        }
        // 
        this.editDialogVisible = false;
        // 
        this.getUserList();
        // 
        this.$message.success('');
      });
    },
    // ID
    async removeUserById (id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(error => error);
      //  confirm
      /// /  concel
      if (confirmResult !== 'confirm') {
        return this.$message.info('');
      }

      const { data: res } = await this.$http.delete('users/' + id);

      if (res.meta.status !== 200) {
        return this.$message.error('');
      }
      this.$message.success('');
      this.getUserList();
    },
    // 
    async setRole (userInfo) {
      this.userInfo = userInfo;

      // 
      const { data: res } = await this.$http.get('roles');
      if (res.meta.status !== 200) {
        return this.$message.error('');
      }

      this.roleList = res.data;

      this.setRoleDialogVisible = true;
    },
    // 
    async saveRoleInfo () {
      if (!this.$message.error) {
        return this.$message.error('');
      }

      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`,
        { rid: this.selectedRoleId });

      if (res.meta.status !== 200) {
        return this.$message.error('');
      }

      this.$message.success('');

      this.getUserList();
      this.setRoleDialogVisible = false;
    },
    // 
    setRoleDialogClosed () {
      this.selectedRoleId = '';
      this.userInfo = {};
    }
  }
};
</script>

<style lang="less" scoped>

</style>
